<template>
    <div class="left">
        <header class="left__title">
            <span/><h2></h2><span/>
        </header>
        <div v-for="{id, points, name, date, link, detail, dec, account, password} in projectList" class="left-item">
            <div class="left-item-header">
                <span>{{date}}</span>
                <h3>{{name}}</h3>
                <!--<div>-->
                    <!--<left-confirm-->
                        <!--:handleConfirm="() => handleClickLink(link)"-->
                        <!--placement="top"-->
                        <!--confirmText=""-->
                    <!--&gt;-->
                        <!--<div slot="content">-->
                            <!--<div>:{{account}}</div>-->
                            <!--<div>:{{password}}</div>-->
                        <!--</div>-->
                        <!--<el-button></el-button>-->
                    <!--</left-confirm>-->
                    <!--<el-button @click="$router.push('/project'+detail)"></el-button>-->
                <!--</div>-->
            </div>
            <p style="margin-bottom: 0">{{dec}}</p>
            <p style="margin-top: 5px">:{{link}} {{account && `(:${account} :${password})`}}</p>
            <div v-for="(point, index) in points" :key="index">
                &nbsp;&nbsp;&nbsp;&nbsp;
                <span class="left__personal-point"/>{{point}}
            </div>
        </div>
        <div class="left__personal">
            <header class="left__title">
                <span/><h2></h2><span/>
            </header>
            <p v-for="(item, index) in traitList" :key="index">
                <!--<span class="left__personal-point"/>-->
                {{` ${index + 1}. ${item}`}}
            </p>
        </div>
    </div>
</template>

<script>
    import config from '@/config';
    import Vue from 'vue';
    import Confirm from './Confirm.vue';

    Vue.component('left-confirm', Confirm);
    const {projectList, traitList} = config;
    export default {
        data() {
            return {
                projectList,
                traitList
            };
        },
        methods: {
            handleClickLink(link) {
                window.open(link);
            }
        }
    };
</script>

<style lang="less" scoped>
    @import 'index';
</style>



